.main h1 {
    padding: 20px;
    text-transform: uppercase;
    text-align: center;
}

.main div {
    text-align: center;
}

.main img {
    animation: logoAnimation 5s infinite linear;
}

.main p {
    padding: 0 20px 10px 20px;
    text-align: center;
    font-weight: bold;
}

@keyframes logoAnimation {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(360deg);
    }
}